import React from 'react'
import { Card } from 'antd';
import { Row, Col } from 'antd';
import CustomerDetails from '../CustomerDetails/CustomerDetails';
import PropertyDetails from '../PropertyDetails/PropertyDetails';
import AdditionalPropertyDetails from "../AdditionalPropertyDetails/AdditionalPropertyDetails";
import CommunicationsSettings from '../CommunicationsSettings/CommunicationsSettings';
import AdditionalCustomerDetails from '../AdditionalCustomerDetails/AdditionalCustomerDetails';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';


import "./AddCustomer.css";

const AddCustomer = () => {
    return (
        <div className="customer_form">
            <Header/>
            <div className="container">
                <h3 className="pageheading">Add customer</h3>  
                <Row gutter={16}>
                    <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                        <CustomerDetails />  
                        <CommunicationsSettings/> 
                        <AdditionalCustomerDetails/> 
                    </Col>
                    <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                        <PropertyDetails /> 
                        <AdditionalPropertyDetails/>  
                    </Col>                    
                </Row>
            </div>
            <Footer/>
           
        </div>
    )
}

export default AddCustomer;

